























import { Component, Prop, Watch, Vue } from 'vue-property-decorator';
import RulePk10 from '@/application/Gamerules/View/RulePk10.vue';
import RuleSSC from '@/application/Gamerules/View/RuleSSC.vue';
import RuleK3 from '@/application/Gamerules/View/RuleK3.vue';
import RuleElevenFive from '@/application/Gamerules/View/RuleK3.vue';
import RuleOthers from '@/application/Gamerules/View/RuleK3.vue';

@Component({
  components: {
    RulePk10,
    RuleSSC,
    RuleK3,
    RuleElevenFive,
    RuleOthers,
  },
})
export default class GameRulesList extends Vue {
  private gameType?: string = '';
  @Prop() public readonly propGameType!: string;
  private mounted() {
    this.gameType = this.propGameType;
  }
  @Watch('propGameType')
  private changePropGameType(v: string) {
    this.gameType = v;
  }
}
